import React from "react";
import { Link } from "react-router-dom";
function Navbar({logo}) {
    return (
        <>
            <nav
                class=" fixed bg-white light:bg-primary-900 w-full z-20 top-0 start-0 border-b border-primary-200 light:border-primary-600">
                <div class="max-w-screen-xl flex flex-wrap items-center justify-between mx-auto p-4">
                    <a href="#" class="flex items-center space-x-3 rtl:space-x-reverse">
                        <img src={logo} class="h-8" alt="DivCraft Logo"/>
                        <span class="self-center text-2xl font-bold whitespace-nowrap text-primary-700">DevCraft</span>
                    </a>
                    <div class="flex md:order-2 space-x-3 md:space-x-0 rtl:space-x-reverse">
                        <a type="button" href="signinform.html"
                            class="text-white bg-primary-700 hover:bg-primary-800 hover:cursor-pointer focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-4 py-2 text-center light:bg-primary-600 light:hover:bg-primary-700 light:focus:ring-primary-800">
                            Login</a>
                        <button data-collapse-toggle="navbar-sticky" type="button"
                            class="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-primary-500 rounded-lg md:hidden hover:bg-primary-100 focus:outline-none focus:ring-2 focus:ring-primary-200 light:text-primary-400 light:hover:bg-primary-700 light:focus:ring-primary-600"
                            aria-controls="navbar-sticky" aria-expanded="false">
                            <span class="sr-only">Open main menu</span>
                            <svg class="w-5 h-5" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none"
                                viewBox="0 0 17 14">
                                <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                                    d="M1 1h15M1 7h15M1 13h15" />
                            </svg>
                        </button>
                    </div>
                    <div class="items-center justify-between hidden w-full md:flex md:w-auto md:order-1" id="navbar-sticky">
                        <ul
                            class="flex flex-col p-4 md:p-0 mt-4 font-medium border border-primary-100 rounded-lg md:space-x-8 rtl:space-x-reverse md:flex-row md:mt-0 md:border-0 dark:bg-primary-900 md:text-primary-white  dark:border-primary-700">
                            <li>
                                <Link to="/" class="block py-2 px-3 md:text-primary-700 hover:text-primary-50 text-primary-50 rounded md:bg-transparent bg-primary-600 hover:bg-primary-700 md:hover:bg-transparent md:hover:text-primary-900 md:p-0 md:dark:hover:text-primary-500 dark:text-white  dark:hover:text-white dark:md:bg-primary-700 ">Home</Link>
                            </li>
                            <li>
                                <Link to="/About" class="block py-2 px-3 md:text-primary-700   text-primary-900 hover:text-primary-900 rounded hover:bg-primary-100 md:hover:bg-transparent md:hover:text-primary-900 md:p-0 md:dark:hover:text-primary-500 dark:text-white dark:hover:bg-primary-700 dark:hover:text-white ">About</Link>
                            </li>
                            <li>
                                <Link to="/Services"
                                    class="block py-2 px-3 text-primary-900 rounded md:text-primary-700 hover:bg-primary-100 md:hover:bg-transparent md:hover:text-primary-900 md:p-0 md:dark:hover:text-primary-500 dark:text-white dark:hover:bg-primary-700 dark:hover:text-white">Services</Link>
                            </li>
                            <li>
                                <Link to="/Contact"
                                    class="block py-2 px-3 text-primary-900 rounded md:text-primary-700 hover:bg-primary-100 md:hover:bg-transparent md:hover:text-primary-900 md:p-0 md:dark:hover:text-primary-500 dark:text-white dark:hover:bg-primary-700 dark:hover:text-white">Contact</Link>
                            </li>
                            <li>
                                <Link to="/Blog"
                                    class="block py-2 px-3 text-primary-900 rounded md:text-primary-700 hover:bg-primary-100 md:hover:bg-transparent md:hover:text-primary-900 md:p-0 md:dark:hover:text-primary-500 dark:text-white dark:hover:bg-primary-700 dark:hover:text-white">Blogs</Link>
                            </li>
                            <li>
                                <Link to="/Help"
                                    class="block py-2 px-3 text-primary-900 rounded md:text-primary-700 hover:bg-primary-100 md:hover:bg-transparent md:hover:text-primary-900 md:p-0 md:dark:hover:text-primary-500 dark:text-white dark:hover:bg-primary-700 dark:hover:text-white">Help</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </>

    )
}
// export default Navbar;
export default Navbar;