
import 'flowbite';
import Loader from './loader/Loader';
import React, { Suspense, lazy } from 'react';
import Navbar from './components/Navbar';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
// Images import start
import logo from './assets/img/logo.png';
import headerImage from "./assets/img/responsive.svg";
import logoWhite from './assets/img/logo-white.png';
import logoBlack from './assets/img/logo-black.png';
import waveSvg from './assets/img/wave.svg';
import waveToBottomSvg from './assets/img/wave-to-bottom.svg';
import waveToTopSvg from './assets/img/wave-top.svg';
import ceoProfile from "./assets/img/CEO_Profile.jpg";
// Images End
// Components 
const Home = lazy(() => import('./components/Home'));
const Blog = lazy(() => import('./components/Blog'));
const Help = lazy(() => import('./components/Help'));
const Footer = lazy(() => import('./components/Footer'));
const About = lazy(() => import('./components/About'));
const PageNotFound = lazy(() => import('./components/PageNotFound'));

let profileInfo = {
    name: "MP Polo",
    src: "https://www.instagram.com/mpbalghari/?hl=en",
    user:"@mpbalghari",
    photo: ceoProfile,
    following: 75,
    followers: 240,
    bio: "Web Developer"
}
function App() {
    return (
        <>

            <Router>
                <Navbar logo={logo} />
                <Suspense fallback={<Loader logo={logo} />}>

                    <Routes>
                        <Route path="/about" element={<About logo={logo}/>} />
                        <Route path="/" element={<Home headerImage={headerImage} />} />
                        <Route path="/blog" element={<Blog img={headerImage} logo={logo} ceoProfile={ceoProfile} src={profileInfo.src} photo={profileInfo.photo} bio={profileInfo.bio} name={profileInfo.name}  followers={profileInfo.followers} following={profileInfo.following} user={profileInfo.user}/>} />
                        <Route path="/help" element={<Help/>} />
                        <Route path="*" element={<PageNotFound />} />
                    </Routes>
                    <Footer logo={logo} />
                </Suspense>
            </Router>

        </>
    )
}

export default App;


